@value makerWhite from '../../common/colors.css';
@value mediaQuery from '../../common/size.css';

.matchingPreviewBox {
  display: flex;
  flex-direction: column;
  width: 50%;
  height: 60vh;
  overflow-y: scroll;
}

.matchingUiBox {
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  margin-top: 10px;
}



.matchingUl {
  margin-right: 30px;;
}

.buttonSubmit {
  width: 40px;
  height: 40px;
  font-size: 1.2rem;
  border: hidden;
  background-color: transparent;
  cursor: pointer;
}

.tagSection {
  width: 100%;
  height: 60px;
  color: black;
  margin-top: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.tagSectionContainer {
  width: 100px;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #e8ebee;
  list-style: none;
  border-radius: 30px;
  margin-right: 10px;
}

.tagSectionContainer a {
  text-decoration: none;
  color: currentColor;
  cursor: pointer;
}

.tagSectionItem {
  color: red;
}

.matchingBox {
  display: flex;
  width: 100%;
  height: 140px;
  border: 1px solid #dee2e6;
  margin-top: 10px;
  margin-bottom: 10px;
}

.gymImg {
  width: 200px;
  height: 140px;
}

.matchigBoxTime {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 25px;
  font-weight: 600;
  width: 20%;
  height: 120px;
  margin-top: 10px;
  margin-right: 1px;
  margin-bottom: 10px;
}

.matchingInfo {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 60%;
  height: 120px;
  margin-bottom: 10px;
  font-size: 25px;
  font-weight: 600;
  margin-left: 30px;
}

.matchingInfo div {
  margin-top: 10px;
}

.significant {
  font-size: 15px;
}

.mattchingUI {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 15px;
}

.mattchingUI button {
  width: 130px;
  height: 35px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #7f7f7f;
  border-radius: 20px;
  margin-bottom: 10px;
  border: none;
  cursor: pointer;
}

.mattchingUI a {
  text-decoration: none;
  color: white;
  font-size: 15px;
}

.mattchingUI div {
  width: 130px;
  height: 35px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #7f7f7f;
  color: white;
  border-radius: 20px;
  margin-bottom: 10px;
}



.matchingBox {
  display: flex;
  align-items: center;
  height: 162px;
  border: 1px solid #dee2e6;
  margin-bottom: 10px;
}


.matchingInfo {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 50%;
  font-size: 15px;
  font-weight: 00;
  margin-left: 10px;
}

.matchingInfo div {
  margin-top: 1px;
}

.matchingUI {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 10%;
  font-size: 15px;
}

.deleteUI {
  width: 50px;
  height: 35px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #e8ebee;
  border-radius: 20px;
  margin-bottom: 10px;
  border: none;
  cursor: pointer;
  text-decoration: none;
  color: black;
  font-size: 15px;
}


@media screen and (max-width: mediaQuery) {
  .matchingPreviewBox {
    width: 100%;
  }
}